@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 28px;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 38px;
  height: 38px;
  padding: 8px 0 6px;
  margin-left: 0;
  color: var(--text-color);
  font-size: 18px;
  line-height: calc(22 / 18);
  font-weight: 700;
  text-decoration: none;
  vertical-align: middle;
  border: 1px solid var(--btn-border-color);
  border-radius: 50%;
  margin-right: 8px;
}

@media only screen and (min-width: 768px) {
  .tui-pagination .tui-page-btn {
    width: 44px;
    height: 44px;
    margin-right: 10px;
  }
}

.tui-pagination .tui-page-btn:hover {
  background-color: #f4f4f4;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: var(--current-btn-text-color);
  background-color: var(--current-btn-bg-color);
  border-color: var(--btn-border-color);
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #4b96e6;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  border-left: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  border-right: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  width: 38px;
  height: 38px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid var(--btn-border-color);
}

@media only screen and (max-width: 374px) {
  .tui-pagination .tui-first,
  .tui-pagination .tui-prev,
  .tui-pagination .tui-next,
  .tui-pagination .tui-last,
  .tui-pagination .tui-prev-is-ellip,
  .tui-pagination .tui-next-is-ellip {
    width: 30px;
    height: 30px;
  }
}

@media only screen and (min-width: 768px) {
  .tui-pagination .tui-first,
  .tui-pagination .tui-prev,
  .tui-pagination .tui-next,
  .tui-pagination .tui-last,
  .tui-pagination .tui-prev-is-ellip,
  .tui-pagination .tui-next-is-ellip {
    width: 44px;
    height: 44px;
  }
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-next-is-ellip {
  border-left: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 1px solid var(--btn-border-color);
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 14px;
}

@media only screen and (min-width: 768px) {
  .tui-pagination .tui-prev {
    margin-right: 24px;
  }
}

.tui-pagination .tui-next {
  margin-left: 6px;
}

@media only screen and (min-width: 768px) {
  .tui-pagination .tui-next {
    margin-left: 16px;
  }
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.tui-pagination .tui-first,
.tui-pagination .tui-first + .tui-prev {
  background-color: var(--light-black-color);
  border-color: transparent;
}

.tui-pagination .tui-next,
.tui-pagination .tui-next + .tui-last {
  background-color: var(--yellow-color);
  border-color: var(--light-black-color);
}
